<script lang="ts">import Icon from 'svelte-awesome';
import { refresh } from 'svelte-awesome/icons';
;
import { UnattributedCellNamesStore } from '../stores/CellNamesStore';
export let cell;
export let free = false;
let unattributedCellNames;
UnattributedCellNamesStore.subscribe((value) => {
    unattributedCellNames = value;
});
function changeCellName(currentCell) {
    const [newCell, ...rest] = [...unattributedCellNames.sort(() => Math.random() - 0.5)];
    cell = newCell;
    UnattributedCellNamesStore.set([...rest, currentCell]);
}
</script>

<style lang="scss">.cell {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #fce9bd;
  border: solid 0.5px #000;
  color: #000;
  text-transform: uppercase;
  font-size: 1.3rem;
  padding: 1rem;
  width: 8rem;
  height: 8rem;
  transition: background-color ease 250ms;
}
.cell:hover {
  background-color: #f9d174;
  cursor: pointer;
}
.cell:hover .actions {
  display: flex;
  position: absolute;
}
.cell.-free {
  cursor: auto;
  background-color: #fff;
}
.cell .actions {
  display: none;
  position: absolute;
  margin: 0.25rem;
  right: 0;
  top: 0;
}</style>

<div class="cell" class:-free={free}>
  {#if !free}
    <div class="actions" on:click={() => changeCellName(cell)}>
      <Icon data={refresh} spin/>
    </div>
  {/if}
  { cell?.name }
</div>

